import React, { useState } from "react";
import {
  Box,
  Typography,
  Button,
  Grid,
  Dialog,
  IconButton
} from "@mui/material";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import CloseIcon from "@mui/icons-material/Close";

const TestimonialsSection = () => {
  const [contentType, setContentType] = useState<"video" | "written">("video");
  const [openVideo, setOpenVideo] = useState<string | null>(null);
  const [showAll, setShowAll] = useState(false);

  // 👉 Just put your 30 Vimeo IDs here
  const vimeoIds = [
    "123456789",
    "987654321",
    "456789123",
    "111222333",
    "444555666",
    "777888999",
    // ... add all 30 Vimeo IDs
  ];

  // Generate testimonials automatically
  const testimonials = vimeoIds.map((id, index) => ({
    id: index + 1,
    name: `Student ${index + 1}`,
    role: "Student",
    videoUrl: `https://player.vimeo.com/video/${id}`,
    thumbnail: `https://vumbnail.com/${id}.jpg`
  }));

  // Only show 6 by default
  const visibleTestimonials = showAll
    ? testimonials
    : testimonials.slice(0, 6);

  return (
    <Box
      sx={{
        background:
          "linear-gradient(180deg, #70A6B2 0%, #70A6B2 50%, white 50%, white 100%)",
        py: { xs: 6, lg: 10 },
        position: "relative"
      }}
    >
      <Box
        sx={{
          maxWidth: "1440px",
          width: "100%",
          mx: "auto",
          px: { xs: 2, md: 4, lg: 6 }
        }}
      >
        {/* Title */}
        <Typography
          variant="h2"
          sx={{
            textAlign: "center",
            fontSize: { xs: "2rem", md: "3rem", lg: "3.5rem" },
            fontWeight: 700,
            color: "white",
            mb: 4
          }}
        >
          Why Students Love One Window
        </Typography>

        {/* Content Toggle */}
        <Box sx={{ display: "flex", justifyContent: "center", mb: 6 }}>
          <Box
            sx={{
              background: "white",
              borderRadius: "25px",
              p: 1,
              display: "flex",
              boxShadow: "0 4px 20px rgba(0,0,0,0.1)"
            }}
          >
            <Button
              variant={contentType === "video" ? "contained" : "text"}
              onClick={() => setContentType("video")}
              sx={{
                borderRadius: "20px",
                px: 3,
                py: 1,
                fontSize: "1rem",
                fontWeight: 600,
                textTransform: "none",
                background:
                  contentType === "video" ? "#FF6B35" : "transparent",
                color: contentType === "video" ? "white" : "#2C3E50",
                "&:hover": {
                  background:
                    contentType === "video"
                      ? "#E55A2B"
                      : "rgba(255, 107, 53, 0.05)"
                }
              }}
            >
              Video
            </Button>
            <Button
              variant={contentType === "written" ? "contained" : "text"}
              onClick={() => setContentType("written")}
              sx={{
                borderRadius: "20px",
                px: 3,
                py: 1,
                fontSize: "1rem",
                fontWeight: 600,
                textTransform: "none",
                background:
                  contentType === "written" ? "#FF6B35" : "transparent",
                color: contentType === "written" ? "white" : "#2C3E50",
                "&:hover": {
                  background:
                    contentType === "written"
                      ? "#E55A2B"
                      : "rgba(255, 107, 53, 0.05)"
                }
              }}
            >
              Written
            </Button>
          </Box>
        </Box>

        {/* Video Cards */}
        {contentType === "video" && (
          <>
            <Grid container spacing={3} justifyContent="center">
              {visibleTestimonials.map((testimonial) => (
                <Grid item xs={12} sm={6} md={4} key={testimonial.id}>
                  <Box
                    sx={{
                      background: "white",
                      borderRadius: "20px",
                      overflow: "hidden",
                      boxShadow: "0 8px 30px rgba(0,0,0,0.12)",
                      transition: "all 0.3s ease",
                      cursor: "pointer",
                      "&:hover": {
                        transform: "translateY(-8px)",
                        boxShadow: "0 12px 40px rgba(0,0,0,0.15)"
                      }
                    }}
                    onClick={() => setOpenVideo(testimonial.videoUrl)}
                  >
                    {/* Thumbnail (Lazy load) */}
                    <Box
                      component="img"
                      src={testimonial.thumbnail}
                      alt={`${testimonial.name} testimonial`}
                      loading="lazy"
                      sx={{
                        width: "100%",
                        height: "200px",
                        objectFit: "cover"
                      }}
                    />

                    {/* Play button overlay */}
                    <Box
                      sx={{
                        position: "absolute",
                        top: "50%",
                        left: "50%",
                        transform: "translate(-50%, -50%)",
                        background: "rgba(0,0,0,0.6)",
                        borderRadius: "50%",
                        p: 2
                      }}
                    >
                      <PlayArrowIcon
                        sx={{ fontSize: "3rem", color: "white" }}
                      />
                    </Box>

                    {/* Content */}
                    <Box sx={{ p: 3 }}>
                      <Typography
                        variant="h6"
                        sx={{
                          fontWeight: 600,
                          color: "#2C3E50",
                          mb: 1
                        }}
                      >
                        {testimonial.name}
                      </Typography>
                      <Typography
                        variant="body2"
                        sx={{
                          color: "#7F8C8D",
                          fontSize: "0.875rem"
                        }}
                      >
                        {testimonial.role}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
              ))}
            </Grid>

            {/* View All Button */}
            <Box sx={{ textAlign: "center", mt: 4 }}>
              <Button
                onClick={() => setShowAll(!showAll)}
                sx={{
                  background: "#FF6B35",
                  color: "white",
                  px: 4,
                  py: 1.5,
                  borderRadius: "25px",
                  fontWeight: 600,
                  "&:hover": { background: "#E55A2B" }
                }}
              >
                {showAll ? "View Less" : "View All"}
              </Button>
            </Box>
          </>
        )}

        {/* Written Content */}
        {contentType === "written" && (
          <Box
            sx={{
              background: "white",
              borderRadius: "20px",
              p: 4,
              textAlign: "center",
              boxShadow: "0 8px 30px rgba(0,0,0,0.12)"
            }}
          >
            <Typography
              variant="h5"
              sx={{ color: "#2C3E50", mb: 3 }}
            >
              Written testimonials coming soon...
            </Typography>
            <Typography
              variant="body1"
              sx={{ color: "#7F8C8D", lineHeight: 1.6 }}
            >
              We're working on collecting written testimonials from our
              students. Check back soon!
            </Typography>
          </Box>
        )}

        {/* Video Popup Modal */}
        <Dialog
          open={!!openVideo}
          onClose={() => setOpenVideo(null)}
          maxWidth="md"
          fullWidth
        >
          <Box sx={{ position: "relative", pb: "56.25%", height: 0 }}>
            <iframe
              src={openVideo || undefined}
              frameBorder="0"
              allow="autoplay; fullscreen; picture-in-picture"
              allowFullScreen
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%"
              }}
            ></iframe>
          </Box>
          <IconButton
            onClick={() => setOpenVideo(null)}
            sx={{
              position: "absolute",
              top: 10,
              right: 10,
              color: "white"
            }}
          >
            <CloseIcon />
          </IconButton>
        </Dialog>
      </Box>
    </Box>
  );
};

export default TestimonialsSection;
